export default "import * as React from 'react';\nimport {Button, FileInput, FileInputSelectors, Form, Section} from 'react-vapor';\n\nconst inputId = 'file-input-example';\n\nexport const FileInputExamples: React.FunctionComponent = () => {\n    function logTheSelectedFile() {\n        // tslint:disable-next-line\n        console.log(FileInputSelectors.getFile(inputId));\n    }\n\n    return (\n        <Form>\n            <Section>\n                <FileInput id={inputId} accept=\".csv,.txt\" placeholder=\"Choose a file...\" />\n                <Button name=\"log file\" primary onClick={logTheSelectedFile} />\n            </Section>\n        </Form>\n    );\n};\n";